:root {
  --body-color: #fff;
  --card-color: #fff;
  --card-border-color: #f3f3f3;
  --skeleton-color: #f2f2f2;
  --overlay-color: #fbfbfb;
  --accent-color: #4440f6;
  --text-color: #111321;
  --white-color: #fff;
  --black-color: #111321;
  --light-grey-color: #7a7a7a;
  --accent-color-read: #00dd73;
  --accent-color-overlay: #4440f7;
  --inactive-color: #5f6775;
  --text-color-light: #9d9d9d;
  --search-color: #fcfcfc;
  --search-placeholder-color: #c4c4c4;
  --search-focus-color: #b7b7b7;
  --search-text-color: #0f0f0f;
  --modal-background-color: #fff;
  --modal-text-color: #111321;
  --cart-dashed-line-color: #dfdfdf;
  --heart-background-color: #fff;
  --sneaker-price-label-color: #bdbdbd;
  --toggle-dark: linear gradient #378fe6 to #3eda82;
  --slider-background-color: #b7b7b7;
  --header-user-color: #5c5c5c;
  --header-user-slider-color: #fff;
  --sneaker-inCart-color: #111321;
}

:root .dark {
  --body-color: #3c3c3c;
  --card-color: #555;
  --card-border-color: transparent;
  --skeleton-color: #3c3c3c;
  --text-color: #e6e6e6;
  --light-grey-color: #3e3e3e;
  --white-color: #fff;
  --color: #f1e7e7;
  --text-color-light: #c4c4c4;
  --search-color: #f4efe9;
  --search-text-color: #000;
  --search-placeholder-color: #919191;
  --search-focus-color: #ddd;
  --modal-text-color: #e6e6e6;
  --cart-dashed-line-color: #a8a8a8;
  --modal-background-color: #555;
  --heart-background-color: #dbdbdb;
  --sneaker-price-label-color: #acacac;
  --toggle-light: #9498ad;
  --slider-background-color: #555;
  --header-user-color: #e6e6e6;
  --header-user-slider-color: #111321;
  --sneaker-inCart-color: #111321;
}

html, body, div, span, applet, object, iframe, h1, h2, h3, h4, h5, h6, p, blockquote, pre, a, abbr, acronym, address, big, cite, code, del, dfn, em, img, ins, kbd, q, s, samp, small, strike, strong, sub, sup, tt, var, b, u, i, center, dl, dt, dd, ol, ul, li, fieldset, form, label, legend, table, caption, tbody, tfoot, thead, tr, th, td, article, aside, canvas, details, embed, figure, figcaption, footer, header, hgroup, menu, nav, output, ruby, section, summary, time, mark, audio, video {
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
  border: 0;
  margin: 0;
  padding: 0;
}

article, aside, details, figcaption, figure, footer, header, hgroup, menu, nav, section {
  display: block;
}

body {
  line-height: 1;
}

ol, ul {
  list-style: none;
}

blockquote, q {
  quotes: none;
}

blockquote:before, blockquote:after, q:before, q:after {
  content: "";
  content: none;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

body, body.light {
  background-color: var(--body-color);
  color: var(--text-color-light);
  font-family: Inter;
  position: relative;
  overflow-x: hidden;
}

body.dark {
  background-color: var(--body-color);
  color: var(--text-color-light);
}

