.switch {
  width: 60px;
  height: 30px;
  display: inline-block;
  position: relative;
}

.switch input {
  opacity: 0;
}

.slider {
  cursor: pointer;
  background-color: #a5d364;
  transition: all .4s;
  position: absolute;
  inset: 0;
}

.slider:before {
  content: "";
  height: 20px;
  width: 20px;
  background-color: var(--header-user-slider-color);
  border: #636363;
  transition: all .4s;
  position: absolute;
  bottom: 5px;
  left: 5px;
}

input:checked + .slider {
  background: linear-gradient(90grad, #378fe6, #3eda82);
}

input:checked + .slider:before {
  transform: translateX(30px);
}

.slider, .round {
  border-radius: 34px;
}

.round:before {
  border-radius: 50%;
}

input:checked + .round:before, .dark {
  background-color: #1e202a;
}

.light {
  background-color: #fff;
}

.toggle-switch {
  justify-content: center;
  align-items: center;
  margin-right: 15px;
  display: flex;
}

